import React from 'react';
import Stargate from '../../img/carousel/stargate.jpg';

class Third extends React.Component{
    render() {
        const stargate = {
            background: `url(${Stargate})`
        };
        return(
            <div className="carousel-item" style={stargate}>
                <div className="box-left"></div>
                <div className="movie-image" style={stargate}></div>
                <div className="title-info">
                    <h2>Stargate Universe</h2>
                    <h3>99% Match 2009-2011 2 Seasons</h3>
                    <p className="overview">Al descubrirse el misterio del noveno chevron y ser atacada la base secreta caro, un grupo de militares y cientficos se ven obligados a abandonarla y a cruzar el Stargate. Llegan entonces al interior de la nave Destiny (Destino), lanzada por Los Antiguos en la poca dorada de su civilizacin. El problema es que el grupo se percata de que la nave no puede regresar a la Tierra de modo que no tendrn ms remedio que sobrevivir a bordo de la nave.</p>

                    <div className="up-down">
                        <i className="fa fa-thumbs-o-up" aria-hidden="true"></i>
                        <i className="fa fa-thumbs-o-down" aria-hidden="true"></i>

                    </div>

                    <p className="overview">Reparto:
                        <span className="hover"> Robert Carlyle</span>,
                        <span className="hover"> Louis Ferreira</span>,
                        <span className="hover"> Brian J. Smith</span>
                    </p>

                    <p className="overview">Gnero:
                        <span className="hover">Ciencia Ficcin</span>,
                        <span className="hover">Fantasa</span>,
                        <span className="hover">Drama</span>
                    </p>
                </div>
            </div>
        )
    }
}

export default Third;


